<template>
    <div id="app">
        <b-navbar toggleable="sm" type="light" variant="light" class="shadow-sm" sticky>
            <div class="container-fluid">
                <b-navbar-brand href="#"><span v-if="no !== 0 && !modalShow && isAuth">( {{ no }} )</span> {{ title
                    }}</b-navbar-brand>
                <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
                <b-collapse id="nav-collapse" is-nav>
                    <b-navbar-nav class="ms-auto">
                        <b-nav-item><strong>{{ browser }}</strong></b-nav-item>
                        <b-nav-item @click="setLogin()" v-if="!isAuth"><strong>Login</strong></b-nav-item>
                        <b-nav-item @click="btnLogoutApp()" v-if="isAuth"><strong>Logout</strong></b-nav-item>
                    </b-navbar-nav>
                </b-collapse>
            </div>
        </b-navbar>
        <div class="mt-3">
            <div class="container-fluid">
                <b-modal v-model="modalShow" hide-header-close no-close-on-esc hide-footer id="modal-center"
                    no-close-on-backdrop centered title="Login">
                    <div class="alert alert-danger" v-if="msg !== ''"><strong>{{ msg }}</strong></div>
                    <input type="password" v-model="kode" class="form-control" :autofocus="modalShow"
                        v-on:keyup.enter="submitLogin()" placeholder="Masukkan Kode...">
                    <button type="button" @click="submitLogin()" :disabled="loading" class="btn btn-primary mt-3">
                        <b-spinner small v-if="loading"></b-spinner> Login
                    </button>
                    <!-- <button type="button" @click="setModal(false)" :disabled="loading" class="btn btn-danger mt-3 ms-2">
                        Cancel
                    </button> -->
                </b-modal>
                <b-modal id="modal-lg" no-close-on-backdrop no-close-on-esc @hidden="hide" scrollable hide-footer
                    v-model="modalLokasi" size="md" title="Lokasi">
                    <pre style="margin-bottom:0">
                            <p v-for="item in lokasi" :key="item.ip">{{ item }}</p>
                        </pre>
                </b-modal>
                <b-modal v-if="isAuth" id="modal-xl" no-close-on-backdrop no-close-on-esc @hidden="hide" scrollable
                    hide-footer v-model="modalList" size="xl" :title="`${modalTitle} (${total})`">
                    <div v-for="(item, index) in listPerson" :key="index">
                        <div v-if="item.app === app">
                            <div v-if="isAuth">
                                <div class="btn-group" role="group" aria-label="Basic">
                                    <button class="btn btn-sm btn-success" @click="btnBrowserSemua(item.data)">
                                        <i class="fa fa-globe" aria-hidden="true"></i>
                                    </button>
                                    <button class="btn btn-sm btn-danger" @click="btnLogoutSemua(item.data)"
                                        v-if="item.isLogout === true">
                                        <i class="fa fa-sign-out" aria-hidden="true"></i>
                                    </button>
                                    <button class="btn btn-sm btn-warning" @click="btnUpdateSemua(item.data)">
                                        <i class="fa fa-refresh" aria-hidden="true"></i>
                                    </button>
                                    <button class="btn btn-sm btn-primary" @click="btnSelesaiSemua(item.data)">
                                        <i class="fa fa-flag-checkered" aria-hidden="true"></i>
                                    </button>
                                    <button class="btn btn-sm btn-success" @click="btnMapsSemua(item.data)">
                                        <i class="fa fa-map-marker" aria-hidden="true"></i>
                                    </button>
                                </div>

                            </div>
                            <div class="table-responsive">
                                <table class="table table-bordered table-hover mt-2 mb-0">
                                    <thead>
                                        <tr>
                                            <th style="width:4%">*</th>
                                            <th>User</th>
                                            <th style="width:15%"></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(val, index) in item.data" :key="index">
                                            <td class="text-center">{{ (index + 1) }}</td>
                                            <td>
                                                <div>
                                                    <i class='fa fa-user'></i>&nbsp;&nbsp;{{ val.user }}
                                                    <i><small style='color:#2aad53;'>
                                                            <timeago :datetime="val.time" :auto-update="60"></timeago>
                                                        </small></i>
                                                </div>
                                                <div v-if="val.status !== ''"><i class='fa fa-desktop'></i>&nbsp;<small
                                                        v-html="val.status"></small></div>
                                                <div><i class='fa fa-globe'></i><small> {{ val.ip }} - <a
                                                            target='_blank' :href="`${val.link}`">{{ val.link
                                                            }}</a></small></div>
                                                <div style="clear:both"></div>
                                            </td>
                                            <td class="text-center">
                                                <div v-if="isAuth" class="btn-group" role="group" aria-label="Basic">
                                                    <button class="btn btn-sm btn-success" @click="btnBrowser(val.key)">
                                                        <i class="fa fa-globe" aria-hidden="true"></i>
                                                    </button>
                                                    <button class="btn btn-sm btn-danger" v-if="val.isLogout === true"
                                                        @click="btnLogout(val.key, val.url)">
                                                        <i class="fa fa-sign-out" aria-hidden="true"></i>
                                                    </button>
                                                    <button class="btn btn-sm btn-warning" @click="btnUpdate(val.key)">
                                                        <i class="fa fa-refresh" aria-hidden="true"></i>
                                                    </button>
                                                    <button class="btn btn-sm btn-primary" @click="btnSelesai(val.key)">
                                                        <i class="fa fa-flag-checkered" aria-hidden="true"></i>
                                                    </button>
                                                    <button class="btn btn-sm btn-success" @click="btnLokasi(val)">
                                                        <i class="fa fa-map-marker" aria-hidden="true"></i>
                                                    </button>
                                                    <button class="btn btn-sm btn-danger"
                                                        v-if="val.isMusnah != 'undefined'" @click="btnMusnah(val)">
                                                        <i class="fa fa-chain-broken" aria-hidden="true"></i>
                                                    </button>
                                                </div>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </b-modal>
                <div class="row" v-if="isAuth && !modalShow && listServerName.length > 0">
                    <div class="col-lg-3 mb-2">
                        <select class="form-control" id="aplikasi" @change="onChange">
                            <option value="0">Semua Aplikasi</option>
                            <option v-for="item in listServerName" :key="item.data" :value="item.data"
                                :selected="item.data == filterApp">{{ item.data }}
                            </option>
                        </select>
                    </div>
                </div>
                <div v-if="isAuth && !modalShow && listPerson.length > 0" class="row" v-masonry
                    transition-duration="0.3s" item-selector=".item">
                    <div v-masonry-tile v-for="item in listPerson" :key="item.key"
                        class="col-lg-3 col-sm-3 col-xs-12 mb-3 item">
                        <div class="card bg-default shadow-sm">
                            <div class="card-header">
                                <label><b v-html="`${item.label} ( ${item.count} )`"></b>
                                    <div style="clear:both"></div>
                                    <small><a target="_blank" :href="`${item.url}`"><u>{{ item.url }}</u></a></small>
                                </label>
                                <div style="clear:both"></div>
                                <div v-if="isAuth" class="mt-1">
                                    <div class="btn-group" role="group" aria-label="Basic">
                                        <button class="btn btn-sm btn-success" @click="btnBrowserSemua(item.data)">
                                            <i class="fa fa-globe" aria-hidden="true"></i>
                                        </button>
                                        <button class="btn btn-sm btn-danger" @click="btnLogoutSemua(item.data)"
                                            v-if="item.isLogout === true">
                                            <i class="fa fa-sign-out" aria-hidden="true"></i>
                                        </button>
                                        <button class="btn btn-sm btn-warning" @click="btnUpdateSemua(item.data)">
                                            <i class="fa fa-refresh" aria-hidden="true"></i>
                                        </button>
                                        <button class="btn btn-sm btn-primary" @click="btnSelesaiSemua(item.data)">
                                            <i class="fa fa-flag-checkered" aria-hidden="true"></i>
                                        </button>
                                        <button class="btn btn-sm btn-success" @click="btnMapsSemua(item.data)">
                                            <i class="fa fa-map-marker" aria-hidden="true"></i>
                                        </button>
                                        <button class="btn btn-sm btn-primary"
                                            @click="btnShowSingle(item.app, item.label)">
                                            <i class="fa fa-list" aria-hidden="true"></i>
                                        </button>
                                    </div>
                                </div>
                            </div>
                            <div v-if="isAuth" class="card-body" style="max-height:35vh;overflow-y:auto;">
                                <ul class="list-group" v-for="value in item.data" :key="value.key">
                                    <li class='list-group-item' style="background: transparent;">
                                        <div>
                                            <i class='fa fa-user'></i>&nbsp;&nbsp;{{ value.user }}
                                            <i><small style='color:#2aad53;'>
                                                    <timeago :datetime="value.time" :auto-update="60"></timeago>
                                                </small></i>
                                        </div>
                                        <div v-if="value.status !== ''"><i class='fa fa-desktop'></i>&nbsp;<small
                                                v-html="value.status"></small></div>
                                        <div><i class='fa fa-globe'></i><small> {{ value.ip }} - <a target='_blank'
                                                    :href="`${value.link}`">{{ value.link }}</a></small></div>
                                        <div style="clear:both"></div>
                                        <div v-if="isAuth" class="btn-group mt-1" role="group" aria-label="Basic">
                                            <button class="btn btn-sm btn-success" @click="btnBrowser(value.key)">
                                                <i class="fa fa-globe" aria-hidden="true"></i>
                                            </button>
                                            <button class="btn btn-sm btn-danger" v-if="value.isLogout === true"
                                                @click="btnLogout(value.key, value.url)">
                                                <i class="fa fa-sign-out" aria-hidden="true"></i>
                                            </button>
                                            <button class="btn btn-sm btn-warning" @click="btnUpdate(value.key)">
                                                <i class="fa fa-refresh" aria-hidden="true"></i>
                                            </button>
                                            <button class="btn btn-sm btn-primary" @click="btnSelesai(value.key)">
                                                <i class="fa fa-flag-checkered" aria-hidden="true"></i>
                                            </button>
                                            <button class="btn btn-sm btn-success" @click="btnLokasi(value)">
                                                <i class="fa fa-map-marker" aria-hidden="true"></i>
                                            </button>
                                            <button class="btn btn-sm btn-danger" v-if="value.isMusnah != 'undefined'"
                                                @click="btnMusnah(value)">
                                                <i class="fa fa-chain-broken" aria-hidden="true"></i>
                                            </button>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row" v-if="no === 0 && !modalShow">
                    <div class="col-md-3 mb-3" v-for="index in 12" :key="index">
                        <b-card class="shadow-sm">
                            <b-skeleton animation="fade" width="65%"></b-skeleton>
                            <b-skeleton animation="fade" width="55%"></b-skeleton>
                            <b-skeleton animation="fade" width="70%"></b-skeleton>
                            <b-skeleton animation="fade" width="90%"></b-skeleton>
                            <b-skeleton animation="fade" width="80%"></b-skeleton>
                        </b-card>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Monitoring',
    props: {
        browser: String,
        level: String,
        charging: Boolean
    },
    data() {
        return {
            title: process.env.VUE_APP_TITLE,
            isConnected: false,
            reconnect: false,
            nomor: 0,
            person: 0,
            no: 0,
            total: 0,
            listPerson: [],
            listSinglePerson: [],
            kode: '',
            msg: '',
            modalShow: false,
            modalLokasi: false,
            modalList: false,
            isAuth: false,
            app: '',
            lokasi: [],
            temp: '',
            modalTitle: 'List',
            loading: false,
            session: '',
            listServerName: [],
            filterApp: 0,
        }
    },
    sockets: {
        connect() {
            this.isConnected = true
        },
        reconnect() {
            this.reconnect = true
        },
        disconnect() {
            this.isConnected = false;
        },
        new_user(data) {
            if (!localStorage.getItem('token')) {
                this.modalShow = true
            } else {
                this.isAuth = true
            }
            this.listPerson = []
            this.no = 0
            if (JSON.parse(data).length > 0) {

                let data_user = JSON.parse(data);
                // let group_to_values = data_user.reduce(function (obj, item) {
                //     obj[item.app_id + '||' + item.app_name + '||' + item.servername] = obj[item.app_id + '||' + item.app_name + '||' + item.servername] || [];
                //     obj[item.app_id + '||' + item.app_name + '||' + item.servername].push({
                //         'uniq': item.username + '' + item.session_id + '' + item.browser + '' + item.sock_id,
                //         'app': item.app_id,
                //         'data': item.username + '||' + item.session_id + '||' + item.browser + '||' + item.datetime + '||' + item.sock_id + '||' + item.host + '||' + item.lat + '||' + item.long + '||' + item.opsi
                //     });
                //     return obj;
                // }, {});


                let group_to_servername = data_user.reduce(function (obj, item) {
                    obj[item.servername] = obj[item.servername] || [];
                    return obj;
                }, {});
                let group_server = Object.keys(group_to_servername).map(function (key) {
                    return {
                        data: key
                    };
                });
                this.listServerName = (group_server)

                let group_to_values;

                if (this.filterApp != 0) {
                    const data_user_filter = data_user.filter(val => val.servername.includes(this.filterApp));
                    group_to_values = data_user_filter.reduce(function (obj, item) {
                        obj[item.app_id + '||' + item.app_name] = obj[item.app_id + '||' + item.app_name] || [];
                        obj[item.app_id + '||' + item.app_name].push({
                            'uniq': item.username + '' + item.session_id + '' + item.browser + '' + item.sock_id,
                            'app': item.app_id,
                            'data': item.username + '||' + item.session_id + '||' + item.browser + '||' + item.datetime + '||' + item.sock_id + '||' + item.host + '||' + item.lat + '||' + item.long + '||' + item.opsi
                        });
                        return obj;
                    }, {});

                } else {
                    group_to_values = data_user.reduce(function (obj, item) {
                        obj[item.app_id + '||' + item.app_name] = obj[item.app_id + '||' + item.app_name] || [];
                        obj[item.app_id + '||' + item.app_name].push({
                            'uniq': item.username + '' + item.session_id + '' + item.browser + '' + item.sock_id,
                            'app': item.app_id,
                            'data': item.username + '||' + item.session_id + '||' + item.browser + '||' + item.datetime + '||' + item.sock_id + '||' + item.host + '||' + item.lat + '||' + item.long + '||' + item.opsi
                        });
                        return obj;
                    }, {});

                }
                let groups = Object.keys(group_to_values).map(function (key) {
                    return {
                        app_id: key,
                        data: group_to_values[key]
                    };
                });

                groups.forEach(val => {


                    this.nomor++;

                    let split = val.app_id.split('||');
                    let xapp;

                    let jumlah = 0;

                    let x;

                    let isLogout = true;

                    val.data.forEach(v => {

                        let split = v.data.split('||');

                        if (x !== v.uniq) {

                            let pecah = null;
                            if (split[8].indexOf('~')) {
                                pecah = split[8].split('~');
                                if (pecah[0] == 'true') {
                                    if (pecah[5] == 'undefined') {
                                        isLogout = false;
                                    }
                                } else {
                                    if (pecah[2] == 'undefined') {
                                        isLogout = false;
                                    }
                                }
                            }

                            jumlah += 1;

                            this.person += 1;

                        }

                        x = v.uniq;

                    });

                    data = []

                    val.data.forEach(v => {

                        let split = v.data.split('||');

                        if (xapp !== v.uniq) {

                            let pecah = null;
                            if (split[8].indexOf('~')) {
                                pecah = split[8].split('~');
                                let status = split[8].split('~')
                                let label_status;
                                if (status[0] == 'true') {
                                    let browser = split[2] + ', '
                                    let ram = ''
                                    if (status[1] != 'undefined') {
                                        ram = status[1] + 'Gb, '
                                    }
                                    let battery = ((status[2] == 'true') ? '<i class="fa fa-battery-full" style="color:green" aria-hidden="true"></i>' : '<i class="fa fa-battery-full" style="color:red" aria-hidden="true"></i>') + ' ' + parseFloat(status[3]) + '%';
                                    label_status = `${browser}${ram}${battery}`
                                } else {
                                    label_status = split[2]
                                }

                                isLogout = true

                                if (pecah[0] == 'true') {
                                    if (pecah[5] == 'undefined') {
                                        isLogout = false;
                                    }
                                    data.push({
                                        app: v.app,
                                        key: split[4],
                                        loc: pecah[0],
                                        lat: split[6],
                                        long: split[7],
                                        url: status[5],
                                        user: split[0],
                                        type: 1,
                                        time: split[3],
                                        status: label_status,
                                        link: split[5],
                                        ip: ((status[0] == 'true') ? status[4] : status[1]),
                                        isLogout: isLogout,
                                        isMusnah: ((pecah[6] != 'undefined') ? `${pecah[6]}` : false)
                                    })
                                } else {
                                    if (pecah[2] == 'undefined') {
                                        isLogout = false;
                                    }
                                    data.push({
                                        app: v.app,
                                        key: split[4],
                                        loc: pecah[0],
                                        lat: split[6],
                                        long: split[7],
                                        url: status[2],
                                        user: split[0],
                                        type: 2,
                                        time: split[3],
                                        status: label_status,
                                        link: split[5],
                                        ip: ((status[0] == 'true') ? status[4] : status[1]),
                                        isLogout: isLogout,
                                        isMusnah: ((pecah[3] != 'undefined') ? `${pecah[3]}` : false)
                                    })
                                }
                            }


                        }
                        xapp = v.uniq;
                        this.no++;
                    });

                    this.listPerson.push({
                        app: val.app_id.split('||')[0],
                        label: split[1],
                        count: jumlah,
                        url: split[2],
                        data: data.reverse(),
                        isLogout: isLogout
                    })

                    if (this.app === val.app_id.split('||')[0]) {
                        this.total = jumlah
                    }
                });

                if (this.isAuth && this.no > 0) {
                    document.title = `(${this.no}) ${process.env.VUE_APP_TITLE}`
                } else {
                    document.title = process.env.VUE_APP_TITLE
                }

            } else {

                document.title = process.env.VUE_APP_TITLE

            }

            this.$redrawVueMasonry()
        },
        tidak(res) {
            this.$swal('Informasi', res.pesan, 'info');
        },
        hehe(res) {
            this.$swal('Informasi', res.pesan, 'info');
        },
        msg(data) {
            if (data.pesan === "sukses") {
                localStorage.setItem("token", btoa(btoa(btoa(data.data))))
                window.location.reload()
            } else {
                this.loading = false
                this.msg = 'Kode Anda salah!'
            }
        },
        token(response) {
            if (response.pesan === 'error') {
                localStorage.removeItem('token')
                window.location.reload()
            }
        }
    },
    methods: {
        setLogin: function () {
            this.modalShow = true;
        },
        setModal: function (ev) {
            this.modalShow = ev;
        },
        onChange: function (event) {
            sessionStorage.setItem('filterApp', event.target.value);
            window.location.reload()
        },
        btnLogoutApp() {
            localStorage.removeItem('token')
            sessionStorage.removeItem('filterApp')
            window.location.reload()
        },
        hide() {
            this.$emit('hide')
        },
        submitLogin() {
            if (this.kode) {
                this.loading = true
                this.$socket.emit('inLogin', this.kode);
            } else {
                this.loading = false
                this.msg = 'Kode Anda salah!'
            }
        },
        btnBrowser(key) {
            this.$swal.fire({
                title: 'Konfirmasi',
                text: "Kamu yakin ingin memberitahu bahwa browser user ini harus segera diupdate?",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Gak jadi!',
                confirmButtonText: 'OK. Lanjutkan!'
            }).then((result) => {
                if (result.isConfirmed) {
                    this.$socket.emit('pesan', key);
                }
            })
        },
        btnLogout(key, link) {
            this.$swal.fire({
                title: 'Konfirmasi',
                text: "Kamu yakin ingin mengeluarkan user ini dari aplikasi?",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Gak jadi!',
                confirmButtonText: 'OK. Lanjutkan!'
            }).then((result) => {
                if (result.isConfirmed) {
                    this.$socket.emit('inLogout', {
                        "kode": key,
                        "url": link
                    });
                }
            })
        },
        btnUpdate(key) {
            this.$swal.fire({
                title: 'Konfirmasi',
                text: "Kamu yakin ingin memberitahu user ini bahwa aplikasi sedang dalam perbaikan?",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Gak jadi!',
                confirmButtonText: 'OK. Lanjutkan!'
            }).then((result) => {
                if (result.isConfirmed) {
                    this.$socket.emit('inUpdate', {
                        "kode": key,
                        "pesan": 'Aplikasi sedang dalam perbaikan. Silahkan tunggu beberapa saat.'
                    });
                }
            })
        },

        btnSelesai(key) {

            this.$swal.fire({
                title: 'Konfirmasi',
                text: "Kamu yakin ingin memberitahu user ini bahwa aplikasi sudah selesai dalam perbaikan?",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Gak jadi!',
                confirmButtonText: 'OK. Lanjutkan!'
            }).then((result) => {
                if (result.isConfirmed) {
                    this.$socket.emit('inSelesai', {
                        "kode": key,
                        "pesan": 'Perbaikan aplikasi telah selesai. Silahkan refresh kembali.'
                    });
                }
            })
        },

        btnLogoutSemua(param) {
            this.$swal.fire({
                title: 'Konfirmasi',
                text: "Kamu yakin ingin mengeluarkan semua user dari aplikasi?",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Gak jadi!',
                confirmButtonText: 'OK. Lanjutkan!'
            }).then((result) => {
                if (result.isConfirmed) {
                    param.forEach(item => {
                        this.$socket.emit('inLogout', {
                            "kode": item.key,
                            "url": item.url
                        });
                    })
                    sessionStorage.setItem('filterApp', 0);
                    window.location.reload()
                }
            })

        },

        btnUpdateSemua(param) {
            this.$swal.fire({
                title: 'Konfirmasi',
                text: "Kamu yakin ingin memberitahu semua user bahwa aplikasi sedang dalam perbaikan?",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Gak jadi!',
                confirmButtonText: 'OK. Lanjutkan!'
            }).then((result) => {
                if (result.isConfirmed) {
                    param.forEach(item => {
                        this.$socket.emit('inUpdate', {
                            "kode": item.key,
                            "url": item.url,
                            "pesan": 'Aplikasi sedang dalam perbaikan. Silahkan tunggu beberapa saat.'
                        });
                    })
                }
            })

        },

        btnSelesaiSemua(param) {
            this.$swal.fire({
                title: 'Konfirmasi',
                text: "Kamu yakin ingin memberitahu semua user bahwa aplikasi sudah selesai dalam perbaikan?",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Gak jadi!',
                confirmButtonText: 'OK. Lanjutkan!'
            }).then((result) => {
                if (result.isConfirmed) {
                    param.forEach(item => {
                        this.$socket.emit('inSelesai', {
                            "kode": item.key,
                            "url": item.url,
                            "pesan": 'Perbaikan telah selesai. Silahkan refresh kembali.'
                        });
                    })
                }
            })

        },

        btnBrowserSemua(param) {
            this.$swal.fire({
                title: 'Konfirmasi',
                text: "Kamu yakin ingin memberitahu bahwa browser semua user harus segera diupdate?",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Gak jadi!',
                confirmButtonText: 'OK. Lanjutkan!'
            }).then((result) => {
                if (result.isConfirmed) {
                    param.forEach(item => {
                        this.$socket.emit('pesan', item.key);
                    })
                }
            })

        },

        btnLokasi(data) {
            this.temp = ''
            this.lokasi = []
            fetch(`https://ipinfo.io/${data.ip}?token=${process.env.VUE_APP_TOKEN}`)
                .then(response => response.json())
                .then(response => {
                    this.lokasi.push(response)
                    this.modalLokasi = true
                })
        },

        btnMapsSemua(param) {
            this.temp = ''
            this.lokasi = []
            param.forEach(val => {
                if (val.ip !== this.temp) {
                    fetch(`https://ipinfo.io/${val.ip}?token=${process.env.VUE_APP_TOKEN}`)
                        .then(response => response.json())
                        .then(response => {
                            this.lokasi.push(response)
                        })
                }
                this.temp = val.ip
            })
            this.modalLokasi = true
        },

        btnShowSingle(key, label) {
            this.listSinglePerson = []
            this.listPerson.forEach(val => {
                if (key === val.app) {
                    this.total = val.count
                    this.listSinglePerson.push(val)
                }
            })
            this.modalList = true
            this.app = key
            this.modalTitle = `${label}`
        },
        btnMusnah(value) {
            this.$swal.fire({
                title: 'Kamu yakin?',
                text: "Kamu akan memusnahkan aplikasi ini.",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Gak jadi!',
                confirmButtonText: 'OK. Lanjutkan!'
            }).then((result) => {
                if (result.isConfirmed) {
                    this.$socket.emit('inMusnah', {
                        "kode": value.key,
                        "url": value.isMusnah,
                    });
                    this.$swal.fire(
                        'Sukses!',
                        'Aplikasi ini berhasil kamu musnahkan.',
                        'success'
                    )
                }
            })
        }
    },
    created() {
        this.filterApp = sessionStorage.getItem('filterApp') !== null ? sessionStorage.getItem('filterApp') : 0;
        if (!localStorage.getItem('token')) {
            this.modalShow = true
            this.isAuth = false
        } else {
            setInterval(() => {
                if (!localStorage.getItem('token')) {
                    this.modalShow = false
                    this.isAuth = false
                    return false
                }

                if(this.filterApp != 0) {
                    let jum = 0;
                    this.listServerName.forEach((el) => {
                        if (el.data === sessionStorage.getItem('filterApp')) {
                            jum = 1;
                        }
                    })
    
                    if(jum === 0) {
                        sessionStorage.setItem('filterApp', 0);
                        this.filterApp = 0;
                        window.location.reload()
                    }
                }
            }, 1000)
            this.isAuth = true
        }

        setInterval(() => {
            this.$redrawVueMasonry()
        }, 100)

        if (this.isAuth) {
            this.$socket.emit('token', {
                'token': atob(atob(atob(localStorage.getItem('token')))),
                'kode': this.$socket.id
            })

            setInterval(() => {
                this.$socket.emit('token', {
                    'token': atob(atob(atob(localStorage.getItem('token')))),
                    'kode': this.$socket.id
                })
            }, 10000)
        }

    },
    watch: {
        isAuth: function () {
            if (!localStorage.getItem('token')) {
                this.modalShow = true
                this.isAuth = false
            } else {
                this.isAuth = true
            }
        }
    }
}
</script>
