<template>
    <div>
        <div class="text-center" :style="{ 'display': installBtn }">
            <div class="install">
                <div>
                    <h5 class="text-black">
                        You must install this application.
                    </h5>
                    <div class="btn btn-primary" @click="installer()" :style="{ 'display': installBtn }">
                        Install
                    </div>
                </div>
            </div>
        </div>
        <Monitoring :browser="browser" />
    </div>
</template>

<script>
import Monitoring from './Monitoring.vue'
import platform from 'platform'
export default {
    name: 'App',
    components: {
        Monitoring
    },
    data() {
        return {
            title: process.env.VUE_APP_TITLE,
            browser: '',
            daya: '',
            charging: false,
            installBtn: "none",
            installer: undefined,
        }
    },
    created() {
        this.browser = platform.description

        if (process.env.NODE_ENV === 'production') {

            let installPrompt;

            window.addEventListener("beforeinstallprompt", e => {
                e.preventDefault()
                installPrompt = e;
                this.installBtn = "block"
            })

            this.installer = () => {
                this.installBtn = "none"
                installPrompt.prompt()
                installPrompt.userChoice.then(result => {
                    if (result.outcome === 'accepted') {
                        console.log("User Accepted")
                    } else {
                        console.log("User Denied")
                    }
                    installPrompt = null
                })
            }

            document.addEventListener('contextmenu', (e) => {
                e.preventDefault();
            });

            document.onkeydown = function (e) {
                if (e.keyCode == 123) {
                    return false;
                }
                if (e.ctrlKey && e.shiftKey && e.keyCode == 'I'.charCodeAt(0)) {
                    return false;
                }
                if (e.ctrlKey && e.shiftKey && e.keyCode == 'C'.charCodeAt(0)) {
                    return false;
                }
                if (e.ctrlKey && e.shiftKey && e.keyCode == 'J'.charCodeAt(0)) {
                    return false;
                }
                if (e.ctrlKey && e.keyCode == 'U'.charCodeAt(0)) {
                    return false;
                }
            }
        }
    },
}
</script>